import React from 'react'

const project = (props) => {

    const { name, stack, year, description, source, demo } = props.data;

    return (
        <div className="project-item">
            <h4>{name}</h4>
            <p className="date">{year}</p>
            <ul>
                {stack.map((tool, index) => {return <li key={index}>{tool}</li>})}
            </ul>
            
            <p>{description}</p>

            <a target="_blank" href={source}>source</a>
            {demo ? <a target="_blank" href={demo}>demo</a> : null}
        </div>
    )
}

export default project;



