import React from 'react'

const contact = (props) => {

    const {email, github} = props.data;

    return (
        <section id="contact">
            <h2>Contact</h2>
            <p>email: <a href={`mailto:${email}`}>{email}</a></p>
            <p>github: <a target="_blank" href={github}>{github}</a></p>
        </section>
    )
}

export default contact;


