import React from 'react'

const about = (props) => {
    return (
        <section id="about">
            <h2>About me</h2>
            <p>
            Hello, my name is Rafa. 
            I am a graduate of the Wrocaw University of Technology. <br/>
            I obtained the title of telecommunication engineer.
            </p>
            <p>
            I'm a self-taught programmer. 
            I'm looking for a job as a fullstack/frontend/backend developer.
            </p>

            <h3>I have basic knowledge about:</h3>
            <ul>
                <li>Frontend
                    <ul>
                    {props.technologies.frontend.map((tool) => <li key={tool}>{tool}</li>)}
                    </ul>
                </li>        
                <li>Backend
                    <ul>
                    {props.technologies.backend.map((tool) => <li key={tool}>{tool}</li>)}
                    </ul>
                </li>
                <li>Other
                    <ul>
                    {props.technologies.other.map((tool) => <li key={tool}>{tool}</li>)}
                    </ul>
                </li>
            </ul>
        </section>
    )
}

export default about;



